@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@700;900&family=Open+Sans:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
}

head{
  max-width:100%;
}

body{
  max-width:100%;
  background-color: #90A4AA;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

body::-webkit-scrollbar {
  display: none;
}

.content-wrapper{
  min-height: 100vh;
}

.homepage-container{
  display: flex;
  flex-direction:column;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  align-items: center;
}

.menu-container{
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2;
}

.toggler{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  opacity: 0;
  z-index: 3;
  cursor: pointer;
}

.hamburger{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items:center;
  z-index: 2;
}

.hamburger>div{
  position: absolute;
  border: none;
  width: 60%;
  height: 5px;
  background-color: white;
  transition: 0.4s;
}

.hamburger>div:before, .hamburger>div:after{
  content: "";
  position: absolute;
  border: none;
  width: 100%;
  height: 5px;
  background-color: white;
  transition: 0.4s;
}

.hamburger>div:before{
  top: -12px;
}

.hamburger>div:after{
  top: 12px;
}

.toggler:checked + .hamburger>div{
  transform: rotate(-135deg);
}

.toggler:checked:hover + .hamburger>div{
  transform: rotate(-225deg);
}

.toggler:checked + .hamburger>div:before, .toggler:checked + .hamburger>div:after{
  transform: rotate(-90deg);
  top:0;
}

.toggler:checked ~ .menu{
  visibility: visible;
}

.toggler:checked ~ .menu > div{
  transform: translateX(-50%) translateY(-50%);
}

.toggler:checked ~ .menu > div > div{
  opacity: 1;
}

.menu{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  visibility: hidden;
  transition: 0.6s;
}

.menu > div {
  position: absolute;
  width: 2500px;
  height: 2500px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translateX(150%) translateY(-50%);
  background-color: black;
  transition: .6s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu > div > div{
  max-width: 90vw;
  max-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  opacity: 0;
}

.menu > div > div > ul > li{
  list-style: none;
}

.menu > div > div > ul > li > a{
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  color: white;
  font-size: 50px;
  text-transform: uppercase;
  display: inline-block;
  transition: 0.3s;
}

.menu > div > div > ul > li > a:hover{
  letter-spacing: 10px;
  text-decoration: line-through;
}

.social-links a img{
  width: 48px;
  height: 48px;
  margin: 2rem 1rem 0 1rem;
}


//Default sizing for phones
.content-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.title-container{
  display: inline-block;
  margin-bottom: 2rem;
  transition: 0.15s;
}

.homepage-container{
  align-self: center;
  justify-self: center;
  max-width: 100%;
}

.homepage-container p{
  font-size: 50px;
  font-weight: 900;
  text-transform: uppercase;
  color: white;
  margin-bottom: 10px;
  line-height:50px;
  overflow-wrap: normal;
}

.homepage-container h1{
  text-transform: uppercase;
  font-size: 45px;
  font-weight: 900;
  line-height: 50px;
}

.homepage-and{
  color: white;
}

.development{
  font-size: 43px;
  letter-spacing: 1px;
}

.face{
  height: 200px;
  position: fixed;
  right: 20px;
  bottom: 20px;
  z-index: -1;
}


.brain{
  position: fixed;
  top: 0.8rem;
  left: 0.8rem;
  height: 50px;
  z-index: 3;
  margin-left: 0;
  margin-top: 0;
  animation-name: pulse;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes pulse {
  0% {filter: drop-shadow(0 0 1.5rem rgb(111, 233, 241));}
  50% {filter: drop-shadow(0 0 2.5rem rgb(111, 233, 241));}
  100% {filter: drop-shadow(0 0 1.5rem rgb(111, 233, 241));}
}

footer{
  background-color: black;
  max-width: 100%;
  position: relative;
  bottom: 0;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid white;
}

footer p{
  font-family: 'Open Sans', sans-serif;
  color: white;
  font-size: 0.7rem;
  padding: 0.7rem;
}

